@import '../../scss/variables';

.title {
  font-weight: 800;
  margin: 35px 0;

  @media screen and (max-width: $mobile-2) {
    font-size: 25px;
    margin: 30px 0;
  }
}

.list {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  margin: 0 -20px;

  @media screen and (max-width: $mobile-4) {
    margin: 0;
  }
}

.lastElement {
  height: 10px;

  .notEmpty & {
    position: absolute;
    bottom: 0;
    bottom: 320px;
  }
}
